.navbar {
    width: 100%;
    height: 60px;
    background-color: rgb(19, 19,19);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 36px;
    position: fixed;
    top: 0;
    z-index: 999;    
}
.quantity {
    color: #222;
    background-color: #fff;
    border-radius: 50%;
    padding: 3px 4px 3px 5px;
    font-size: 10px;
    font-weight: 700;
    position:absolute;
    right:28px;
    top: 10px;
    border: #222 2px solid;
}
.links {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}
.links a {
    color: #444;
}
.links > a:last-child {
    background-color: #fff;
    border-radius: 50%;
    padding: 6px 5px 4px 5px;
}
.links > a:last-child:hover {
    transform: scale(1.1);
    transition: all 0.3s ease 0s;
}

.links > a:first-child {
    background-color: #fff;    
    font-size: 18px;
    font-family: "Oswald", sans-serif;
    border-radius: 15px;
    padding: 4px 15px;
}

