.cartItem {
    display: flex;
    justify-content: space-evenly;    
    background-color:   #E6E6E6; 
    border-radius: 5px;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.4);
}
.cartItem img {
    width: 100px;
    height: 100px;
    object-fit: cover;
    border-radius: 5px 0 0 5px;
}
.cartItem .itemDetails {
    flex: 1;
    margin-left: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

}
.cartItem .itemDetails h3 {
    font-size: 1rem;
    font-weight: normal;
}
.cartItem .itemDetails p {
    font-size: 0.8rem;
    font-weight: 700;
}
.cartItem .countHandler {
    display: flex;
    align-items: center;
    gap: 10px;
}
.cartItem .countHandler button {
    border: none;
    background-color: #333;
    cursor: pointer;
    font-size: 1rem;
    border-radius: 50%;
    width: 15px;
    height: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #E6E6E6;
    text-align: center;
    line-height: 0;
}