@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap');
/*reset styles*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
/*no decorators*/
a {
  text-decoration: none;
}
/*no bullet points*/
ul {
  list-style: none;
}
/*no outline*/
*:focus {
  outline: none;
}
/*font family*/
body {
  font-family: 'Poppins', sans-serif;
}
/*Container*/
.container {
  width: 100%;
  max-width: 1170px;
  padding: 0 15px;
}
/*Mobile container*/
@media (max-width: 768px) {
  .container {
    padding: 0 10px;
  }
}
.page {
  margin-top: 60px;
}
