.hero {
    width: 100%;
    height: calc(100vh - 60px);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 60px;
}
/*Contenedor del texto*/
.hero::before {
    content: "";
    position: absolute;
    width: 100%;
    height: calc(100vh - 60px);
    background-image: url("../../assets/banner.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    filter: blur(1.5px);
}
.hero::after {
    content: "";
    position: absolute;
    width: 100%;
    height: calc(100vh - 60px);
    background-color: rgba(0, 0, 0, 0.5);
}
.hero-text {
    position: relative;
    z-index: 1;
    color: #fff;
    text-align: center;
}
.hero-text h1 {
    font-size: 80px;
    font-weight: 600;
    margin-bottom: 20px;
    font-family: 'Oswald', sans-serif;
}
/*flecha para ir a la section de productos que esta abajo*/
.hero-text a {
    color: #fff;
    z-index: 1;
    /*Ponerlo abajo*/
    position: absolute;
    bottom: -200px;
    left: 45%;
    cursor: pointer;
}
.hero-text a:hover  {
    transform: scale(1.1);
    transition: all 0.3s ease 0s;
}