.cardProd {
    width: 200px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);    
}
.cardProd img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.cardProd .info {
    padding: 0.5rem;
    height: fit-content;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    gap: 0.5rem;
}
.cardProd .info h3 {
    /*Nombre del product*/
    font-size: 1rem;
    font-weight: 700;
    text-align: center;
    color: #333;

}

.cardProd .info p {
    font-size: 0.8rem;
    font-weight: 700;
}
.cardProd .addToCartBtn {
    background-color: transparent;
    border: 2px solid #333;
    color: #333;    
    min-width: 100px;
    padding: 5px 10px 5px 10px;
    border-radius: 15px;
    transition: all 0.2s ease-in-out;
}

.cardProd .addToCartBtn:hover {
    background-color: #333;
    color: #fff;
    cursor: pointer;
}