.cartDetails {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    gap: 20px;
}
.cartDetails .buttons {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
}
.cartDetails .buttons button {
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    background-color: #333;
    color: #E6E6E6;    
}